@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/*-----------colours----------*/
/* BRC red: #ef1829;*/
/*-----------mix-ins----------*/
.smoothTransition {
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.button {
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
.button-small {
  padding: 0.7em 0.5em 0.7em 0.5em;
  margin: 0.5em 0.5em 0.5em 0.5em;
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
.button-small:hover {
  background-color: #097B95;
  color: white;
}
.button-large {
  padding: 1.2em 2.2em 1.2em 2.2em;
  margin: 1em 0.5em 5em 0.5em;
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
.clickable-cell {
  border-bottom: #b1a9a2 1px solid;
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
.clickable-cell:hover {
  background-color: #b0b8bd;
}
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-between;
}
/*------------ flex -----------*/
.flex-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-between;
}
.flex-row {
  width: 100%;
  padding: 0.7em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-between;
}
/*----------- @media ----------*/
@media only screen and (device-width: 768px), only screen and (max-width: 768px) {
  body {
    font-size: 15px;
  }
}
/*----------- default css fixing ----------*/
body {
  overflow: hidden;
  box-sizing: border-box;
  text-align: center;
  font-size: 15px;
  margin: auto;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}
a {
  text-decoration: none;
}
.App {
  height: 100vh;
  width: 100vw;
  overflow-y: auto;
  overflow-x: hidden;
}
#instructions {
  font-size: 14px;
  text-align: left;
  padding: 10px;
  background-color: #f1f1f1;
  border-radius: 3px;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.show {
  display: block;
}
.hidden {
  display: none;
}
.title {
  background-color: #eb452f;
  margin: 0px;
  color: #ffffff;
  box-shadow: 0px 2px 5px 0px #b0b8bd;
  padding: 1.2em;
  font-weight: 700;
}
/*-------------------- NAVIGATION BUTTONS ---------*/
.NavButton {
  font-size: 20px;
  color: #ffffff;
  background-color: #02b196;
  padding: 1.2em 2.2em 1.2em 2.2em;
  margin: 1em 0.5em 5em 0.5em;
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
.NavButton:hover {
  background-color: #057a64;
}
.NavButtonGreyed {
  font-size: 20px;
  color: #f1f1f1;
  background-color: #bdb6b0;
  padding: 1.2em 2.2em 1.2em 2.2em;
  margin: 1em 0.5em 5em 0.5em;
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
/*-------------------- TAXONOMY BUTTONS ---------*/
.TaxButton {
  padding: 2em 2.5em 2em 2.5em;
  margin: 1em 1em 1em 1em;
  border-bottom: #b1a9a2 1px solid;
  border-radius: 4px;
  cursor: pointer;
  max-width: 30%;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 3px 5px 0px #b0b8bd;
}
.colorOn {
  background-color: #097B95;
  color: #ffffff;
}
.colorOn:hover {
  background-color: #053f4d;
}
.colorOff {
  background-color: #bdb6b0;
}
.colorOff:hover {
  background-color: #9a8f86;
}
/*-------------------- FORM STYLING ---------*/
input {
  border-top: none;
  border-right: none;
  border-bottom: #98192a 2px solid;
  border-left: none;
  height: 3em;
  font-size: inherit;
  width: 70%;
}
.PageContent {
  width: 100%;
  padding-top: 2em;
}
/*-------------------- Table styling ---------*/
.scrollable-table {
  display: block;
  overflow: auto;
  max-width: 80%;
  max-height: 15em;
  box-shadow: 0px 1px 6px 0px #b0b8bd;
  border-radius: 5px;
}
.scrollable-table tr {
  height: 3em;
}
.scrollable-table tr:first-of-type + tr {
  font-weight: 500;
}
.scrollable-table td {
  min-width: 10em;
}
.scrollable-table tr:nth-child(odd) {
  background: #ffffff;
}
.scrollable-table tr:nth-child(even) {
  background: #f1f1f1;
}
.temp {
  padding: 3em;
}
/*-------------------- General styling ---------*/
/*needs keyframe animation below*/
.waiting {
  color: #98192a;
}

